.Hero{
    display:flex;

 flex-direction: column;
 align-items:center;   
    align-self: center;
    width:100vw;

padding-top: 100px;
padding-bottom: 200px;
background-size: 300% 300%;
background-image: linear-gradient(
          -45deg, 
          #ffb88e 0%, 
          rgb(255, 161, 73) 25%, 
          rgb(228, 199, 167) 51%, 
          #ffb170 100%
    );  
    animation: AnimateBG 20s ease infinite;


}

@keyframes AnimateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }

.Hello{
    padding-top: 200px;
    font-weight:900;
    font-size:3em;
    color:rgb(39, 39, 39);
}

.Name{
    color:rgb(39, 39, 39);
    font-weight:900;
    font-size:3em;
}

.HeroContact{
    padding-top: 50px;
    color:rgb(39, 39, 39);
    font-weight:400;
    font-size:1.5em;
}


